@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
.header {
  margin: 0 2.5rem;
  padding: 0.5rem 0;
  display: flex;
  justify-content: space-between;
  margin-left: 0px;
  margin-right: 0px;
  background-color: #c54b35;
  color: white;
}

.headerLeft,
.headerRight {
  display: flex;
  align-items: center;
}

a > span {
  margin: 0 30px;
  font-size: 1.3rem;
  cursor: pointer;
  color: black;
  text-decoration: none;
  font-family: 'Beau Rivage', cursive;
}
a > span:hover {
  color: lightgreen;
  text-decoration: underline;
  transition: color 0.5s ease-in-out, -webkit-text-decoration 0.3s ease-in-out;
  transition: color 0.5s ease-in-out, text-decoration 0.3s ease-in-out;
  transition: color 0.5s ease-in-out, text-decoration 0.3s ease-in-out, -webkit-text-decoration 0.3s ease-in-out;
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  
  
}




.header__icon {
  width: 80px;
  cursor: pointer;
  margin-left: 10px;
}

.user__avatar {
  font-size: 2rem;
}

.headerRight form {
  display: flex;
  align-items: center;
}

.headerRight input[type="text"] {
  padding: 0.4rem;
  font-size: 1rem;
  border-radius: 4px;
  border: 1px solid gray;
  margin-right: 0.5rem;
  width: 200px;
  transition: width 0.3s ease-in-out;
}

.headerRight input[type="text"]:focus {
  width: 300px;
}

.headerRight button[type="submit"] {
  padding: 0.4rem 1rem;
  background-color: #4caf50;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s ease-in-out;
}

.headerRight button[type="submit"]:hover {
  background-color: #45a049; /* Updated background color on hover */
  -webkit-transform: scale(1.05);
          transform: scale(1.05); /* Added scale transformation on hover */
}

/* Added style for the active button */
.headerLeft a.active > span {
  color: lightgreen;
  text-decoration: underline;
}

@media screen and (max-width: 600px) {
  .headerLeft,
  .headerRight {
    flex-direction: column;
    align-items: flex-start;
  }

  .headerRight input[type="text"] {
    width: 150px;
  }

  a > span {
    margin: 0;
  }
}

.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1rem;
}

.headerLeft {
  display: flex;
  align-items: center;
}

.header__icon {
  width: 80px;
  cursor: pointer;
}

.headerLeft > * {
  margin-right: 1rem;
}

.headerRight form {
  display: flex;
  align-items: center;
}

.headerRight input[type="text"] {
  padding: 0.4rem;
  font-size: 1rem;
  border-radius: 4px;
  border: 1px solid gray;
  margin-right: 0.5rem;
}

.headerRight button[type="submit"] {
  padding: 0.4rem 1rem;
  background-color: #4caf50;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.headerRight button[type="submit"]:hover {
  background-color: #45a049;
}

.posterImage{
    height: 600px;
}

.posterImage > img {
    margin: auto;
    display: block;
    width: 100%;
    margin-top: 50px;
}

.posterImage__overlay {
    position: absolute;
    padding: 10rem;
    bottom: 0px;
    height: 70%;
    display: flex;
    flex-direction: column;
    width: 100%;
    justify-content: flex-end;
    align-items: flex-start;
    background-image: linear-gradient(rgb(0,0,0,0), rgb(0, 0, 0));
    opacity: 1;
    transition: opacity .3s;
    
}   

.posterImage__overlay:hover {
    opacity: 1;
}

.posterImage__title {
    font-weight: 900;
    font-size: 4rem;
    margin-bottom: 0.4rem;
    text-align: left;
}

.posterImage__runtime {
    font-size: 2rem;
    margin-bottom: 1rem;
}

.posterImage__rating {
    margin-left: 3rem;
}

.posterImage__description {
    font-style: italic;
    font-size: 1rem;
    margin-bottom: 0.25rem;
    display: flex;
    text-align: left;
    width: 50%;
}

.movie__list {
    padding: 0 3rem 3rem 3rem;
}

.list__title {
font-size: 1.75rem;
margin:  2.5rem;
}

.list__cards {
    display: flex;
    flex-wrap: wrap ;
    justify-content: center;
}
.cards {
    display: inline-block;
    transition: -webkit-transform .2s;
    transition: transform .2s;
    transition: transform .2s, -webkit-transform .2s;
    position: relative;
    border-radius: 10px;
    overflow: hidden;
    margin: 0.19rem;
    cursor:pointer;
    min-width:200px;
    height:300px;
    z-index:0;
    border: 1px solid rgb(99, 99, 99);
}

.cards:hover {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    z-index: 1000;
    box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
}

.cards__img {
    height: 300px;
}

.cards__overlay {
    position: absolute;
    padding: 0 1rem 1rem 1rem;
    bottom: 0px;
    height: 290px;
    display: flex;
    flex-direction: column;
    width: 85%;
    justify-content: flex-end;
    background-image: linear-gradient(rgb(0,0,0,0), rgb(0,0,0,1));
    opacity: 0;
    transition: opacity .2s;
}   

.cards__overlay:hover {
    opacity: 1;
}

.card__title {
    font-weight: 900;
    font-size: 1rem;
    margin-bottom: 0.4rem;
}

.card__runtime {
    font-size: .75rem;
    margin-bottom: 0.25rem;
}

.card__rating {
    float: right;
}

.card__description {
    font-style: italic;
    font-size: .75rem;
    margin-bottom: 0.25rem;
}
.movie {
    width: 100%;
    position: relative;
    display: flex;
    flex-direction:column;
    align-items: center;
}

.movie__intro {
    width: 80%;
}

.movie__backdrop {
    width: 100%;
    height: 500px;
    object-fit:cover;
    object-position: 0 35%;
}

.movie__detail {
    align-items: center;
    width: 75%;
    display: flex;
    position: relative;
    bottom: 225px;
}

.movie__detailLeft {
    margin-right: 30px;
}

.movie__poster {
    width:300px;
    border-radius: 10px;
    box-shadow: rgba(0, 0, 0, 0.86) 0px 22px 40px 6px;
}

.movie__detailRight {
    color: white;
    display: flex;
    flex-direction: column;
    height: 450px;
    justify-content:space-between ;
}

.movie__detailRightTop > div{
    text-shadow: 0px 0px 5px #000000;
    margin-bottom: .5rem;
}

.movie__name {
    font-weight: 600;
    font-size: 3rem;
}

.movie__voteCount {
    margin-left: 1rem;
}

.movie__genres {
    margin: 1.25rem 0;
}

.movie__genre {
    padding: .5rem;
    border: 2px solid white;
    border-radius: 20px;
    margin-right: 1rem;
}


.movie__detailRightBottom {
    margin: 2rem 0;
    flex: 0.8 1;
}

.synopsisText {
    font-size: 1.5rem;
    margin-bottom: 1.25rem;
    font-weight: 600;
    display: flex;
    position: relative;
    align-items: center;
}
.synopsisText > div:last-of-type {
    margin-left: auto;
}

.movie__links {
    position: relative;
    bottom: 120px;
    display: flex;
    justify-content: space-between;
    width: 75%;
}

.movie__heading {
    font-size: 2.2rem;
}

.movie__Button {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0.8rem 2rem;
    border-radius: 20px;
    cursor: pointer;
    width: 150px;
    color: black;
    font-weight: bold;
}

.movie__homeButton {
    background-color:rgb(255, 0, 0);
    
}

.movie__imdbButton {
    background-color:#f3ce13;
}

.newTab {
    margin-left: 1.4rem;
}

.movie__production {
    width: 85%;
    display: flex;
    justify-content: center;
    align-items: flex-end;
    margin-bottom: 4rem;
}


.movie__productionComapany {
    width: 200px;
    margin: 2rem;
}

.productionCompanyImage {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.preferences-container {
  width: 100%;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  color: black;
  font-family: 'Beau Rivage', cursive;
  
}

.preferences-container h2 {
  font-size: 4rem;
  margin-bottom: 2rem;
  align-items: center;

}

.preferences-container form {
  width: 75%;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 2rem;
}

.preferences-container label {
  display: flex;
  flex-direction: column;
  margin-bottom: 1rem;
}

.preferences-container label span {
  font-size: 1.8rem;
  font-weight: bold;
  margin-bottom: 0.5rem;
}

.preferences-container input {
  width: 100%;
  padding: 1rem;
  font-size: 1.6rem;
  border-radius: 30px;
  border: 1px solid #ccc;
}

.preferences-container button[type="submit"] {
  padding: 1rem 2rem;
  font-size: 1.6rem;
  font-weight: bold;
  border-radius: 20px;
  cursor: pointer;
  background-color: #f3ce13;
  border: none;
  color: black;
}

.recommendations-container {
  width: 75%;
}

.recommendations-container h3 {
  font-size: 2.2rem;
  margin-top: 100px;

}

.movie__recommendationsList {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  grid-gap: 1rem;
}

.movie__recommendation {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.movie__recommendationPoster {
  width: 200px;
  border-radius: 10px;
  box-shadow: rgba(0, 0, 0, 0.86) 0px 22px 40px 6px;
}

.movie__recommendationTitle {
  font-size: 1.8rem;
  font-weight: 600;
  margin-top: 0.5rem;
  text-align: center;
}

