@import url('https://fonts.googleapis.com/css2?family=Montserrat&display=swap');
.header {
  margin: 0 2.5rem;
  padding: 0.5rem 0;
  display: flex;
  justify-content: space-between;
  margin-left: 0px;
  margin-right: 0px;
  background-color: #c54b35;
  color: white;
}

.headerLeft,
.headerRight {
  display: flex;
  align-items: center;
}

a > span {
  margin: 0 30px;
  font-size: 1.3rem;
  cursor: pointer;
  color: black;
  text-decoration: none;
  font-family: 'Beau Rivage', cursive;
}
a > span:hover {
  color: lightgreen;
  text-decoration: underline;
  transition: color 0.5s ease-in-out, text-decoration 0.3s ease-in-out;
  transform: scale(1.1);
  
  
}




.header__icon {
  width: 80px;
  cursor: pointer;
  margin-left: 10px;
}

.user__avatar {
  font-size: 2rem;
}

.headerRight form {
  display: flex;
  align-items: center;
}

.headerRight input[type="text"] {
  padding: 0.4rem;
  font-size: 1rem;
  border-radius: 4px;
  border: 1px solid gray;
  margin-right: 0.5rem;
  width: 200px;
  transition: width 0.3s ease-in-out;
}

.headerRight input[type="text"]:focus {
  width: 300px;
}

.headerRight button[type="submit"] {
  padding: 0.4rem 1rem;
  background-color: #4caf50;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s ease-in-out;
}

.headerRight button[type="submit"]:hover {
  background-color: #45a049; /* Updated background color on hover */
  transform: scale(1.05); /* Added scale transformation on hover */
}

/* Added style for the active button */
.headerLeft a.active > span {
  color: lightgreen;
  text-decoration: underline;
}

@media screen and (max-width: 600px) {
  .headerLeft,
  .headerRight {
    flex-direction: column;
    align-items: flex-start;
  }

  .headerRight input[type="text"] {
    width: 150px;
  }

  a > span {
    margin: 0;
  }
}

